.flex-centred{
	padding-top: 5px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.grid{
	display: grid;
	grid-template-rows: minmax(50px, 200px) 1fr auto;
	grid-template-columns: minmax(50px, 10vw) 1fr minmax(50px, 10vw);
	grid-gap: 30px;
	width: 100%;
}

.full-grid-row{
	grid-column-start: 1;
	grid-column-end: 4;
	background-color: rgba(200,200,200,0.3);
}

.centred-grid-row{
	grid-column-start: 2;
	grid-column-end: 3;
	border-radius: 10px;
	background-color: rgba(200,200,200,0.3);
}

.row2{
	grid-row-start: 1;
	grid-row-end: 2;
}

.row3{
	grid-row-start: 2;
	grid-row-end: 3;
}

.row4{
	grid-row-start: 3;
	grid-row-end: 4;
}

.review-btn-wrapper{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	background-color: white;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.review-btns{
	display: -ms-flexbox;
	display: flex;
	color: black;
	width: 49.5%;
	-ms-flex-item-align: center;
	    align-self: center;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	max-height: 20%;
	min-height: 50px;
	cursor: pointer;
	border-top-right-radius: 10px;
	border-top-left-radius: 10px;
	background-color: rgba(200,200,200,0.3);
	-webkit-transition: all 300ms;
	-o-transition: all 300ms;
	transition: all 300ms;
}

.active-review-btn{
	color: white;
	background-color: rgb(23, 26, 45);
}

.review-system-wrapper{
	background-color: rgb(23, 26, 45);
}


