.see-component-wrapper{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.see-reviews-wrapper{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	width: 100%;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	margin: 20px;
}

.review-stars{
	width: 45%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	margin:10px;
	padding:10px;
	background-color: white;
	color: rgb(23, 26, 45);
	border-radius: 10px;
}

.submit-review-btn{
	padding:10px;
	border-radius: 10px;
	margin-bottom: 10px;
	background-color: rgba(255,255,255,0.6);
	color: rgb(23, 26, 45);
	-webkit-transition:all 500ms;
	-o-transition:all 500ms;
	transition:all 500ms;
}

.submit-review-btn:hover{
	background-color: white;
	cursor: pointer;
}


